import React from "react";
import "./DarkMode.css";

import sun from '../images/flower.png';
import {themeContext} from "./Dmode";
import {useContext} from "react";

export const DarkMode = () => {
    const theme=useContext(themeContext);
    const darkMode=theme.state.darkMode;
    const handleClick =()=>{
        theme.dispatch({type:'click'});
    }

    return (
        <div className={"DarkMode"} onClick={handleClick}>
            <img src={sun} className={"night"}/>
            <img src={sun} className={"day"}/>
            <div className={"d-b"}  style={darkMode ? {left: '2px'} : {right: '2px'}}>

            </div>
        </div>
    )
}

export default DarkMode;