import React from 'react';
import "./navbar.css";
import DarkMode from "../Light-Dark/DarkMode";
const Navbar = () => {

    const serv =() =>{
        document.getElementById('serv').scrollIntoView();
    }

    const cont= ()=>{
        document.getElementById('cont').scrollIntoView();
    }

    const port=()=>{
        document.getElementById('port').scrollIntoView();
    }

    return (

        <nav className={"nav"}>
            <div className={"left_nav"}>

                <a className={"but"} href={"https://github.com/PArDaLaS"}>Github</a>
                <DarkMode/>
            </div>

            <div className={"right_nav"}>

                <div className={"list"}>
                    <ul style={{listStyleType: 'none'}}>
                        <li >Home</li>
                        <li input type={"button"} onClick={serv} >
                            Services
                        </li>
                        <li input type={"button"} onClick={port} >Portfolio</li>
                    </ul>
                </div>
                <button className={"but2"} input type={"button"} onClick={cont} >Contact</button>
            </div>

        </nav>
    );
}
export default Navbar;