import React, {useState} from 'react';
import { useRef } from 'react';
import './contact.css';
import emailjs from '@emailjs/browser';
;
export const Contact = () => {
    const form = useRef()
    const [done, setDone]=useState(false);
    const sendEmail = (e) => {
        e.preventDefault();
        ;
        emailjs.sendForm('service_lyrcq6x', 'template_pfzxzh5', form.current, 'LTRhfH8MKL9VBrXl5')
            .then((result) => {
                console.log(result.text);
                setDone(true);
                document.getElementById("sub").disabled='true';
            }, (error) => {
                console.log(error.text);
            });
    };


    return (
        <div className={"wrap"} id={"cont"}>
        <div className={"contact"} >
            <div className={"c-l"}>
                <span> Get in touch with me!</span>
            </div>
            <div className={"c-r"}>
                <form ref={form} onSubmit={sendEmail}>

                        <div className={"shadow"}></div>

                    <input type={"text"} className={"name"} name={"user_name"} placeholder={"Name"} required/>
                    <input type={"email"} className={"email"} name={"user_email"} placeholder={"Email"} required/>
                    <textarea className={"message"} name={"message"} placeholder={"Message"} required/>
                    <input type={"submit"} value={"Send"} className={"button"} id={"sub"}/>

                    <span>{done && "Thanks for contacting me!"}</span>

                </form>
            </div>
        </div>
        </div>
    )
}
export default Contact;