import React from "react";
import './services.css';
import CV from './Legit_CV-.pdf';
const Services = () =>{
    return(
        <div className="services"  >
            <div className="s-top" id={"serv"}>
                <span className="ms"> My Services</span>
                <span className="s-v">These are the services i provide/want to provide</span>

            </div>
            <div className="s-bot">
                <span>Web Design (HTML, css, Javascript,React)</span>
                <span>Java development</span>
                <span>Data entry and Virtual Assistance</span>
            </div>
            <a className={'dl'} href={CV} download>
                <button className="cv">download CV </button>
            </a>


        </div>
    );
}

export default Services;